.card {
    background-color: rgba(245, 245, 245, 0);
}

.card-header, .card-footer {
    opacity: 1
}

.container {
    padding-right: 50px;
    padding-left: 50px;
}


.about-box {
    width: 100%;
    padding-right: 50px;
    padding-left: 0;
    margin-right: 50px;
    margin-left: 0;
}

@media (min-width: 576px) {
    .about-box {
        max-width: 540px;
    }
}

@media (min-width: 768px) {
    .about-box {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .about-box {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .about-box {
        max-width: 1400px;
    }
}

