.slider-dots li button {

        background-color: #F8F3EB;
        border: none;
        color: white;
        padding: 3px;
        display: inline-block;
        font-size: 0;
        margin: 4px 5px;
        border-radius: 80%;
    }

    .slider-dots li {
        display: inline;
    }

    .slider-dots button {
        display: none;
    }

    .slick-active button {
        /*-webkit-box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.7);*/
        /*box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.7);*/
        box-shadow: 0 0 10px 5px rgba(255, 255, 255,0.9);

    }

    .slick-active LI {
        display: none;
    }

    .slider-dots {
        margin-top: 50px;
        margin-right: 70vw;
        direction: ltr;
    }

    .khat-mashy ul{
        list-style-image: url("assets/images/li.png");
    }